/* ----------------------------------- COLORS ----------------------------------- */
h1, h2, nav {
  font-family: "Raleway", sans-serif;
  font-weight: 700;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 1.75rem;
  line-height: 2.2rem;
}

h2 {
  font-size: 1rem;
  margin-bottom: 1.5rem;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
}

p {
  font-size: 0.95rem;
}

input {
  padding: 0.7rem 0;
  border: 1px solid;
  border-radius: 0.2rem;
  font-family: "Open Sans", sans-serif;
}
input[type=email] {
  padding-left: 1rem;
  color: hsl(243deg, 87%, 12%);
  border-color: rgba(7, 4, 57, 0.75);
}
input[type=email]:focus {
  outline: none;
}
input[type=email]:invalid {
  border: 1px solid hsl(0deg, 48%, 51%);
}
input[type=submit] {
  background-color: hsl(224deg, 93%, 58%);
  color: hsl(0deg, 0%, 100%);
  font-weight: 700;
  border-color: hsl(224deg, 93%, 58%);
  box-shadow: 0 3px 10px 0 hsla(0deg, 0%, 0%, 0.2);
  cursor: pointer;
  transition: background-color 0.2s ease-in-out;
}
input[type=submit]:hover {
  background-color: #628af9;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  color: hsl(243deg, 87%, 12%);
}

body {
  background-color: hsl(0deg, 0%, 100%);
  text-align: center;
}

.container {
  width: calc(100% - 50px);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

header {
  display: flex;
  justify-content: center;
}
header .container {
  padding: 1.5rem 0;
}
header .container img {
  max-width: 80px;
  object-fit: contain;
}
header .container nav ul {
  display: flex;
  gap: 1.5rem;
}
header .container nav ul li {
  position: relative;
}
header .container nav ul li::after {
  content: "";
  width: 0;
  height: 1px;
  background-color: hsl(170deg, 45%, 43%);
  position: absolute;
  bottom: 0;
  left: 0;
  transition: width 0.2s ease-in-out;
}
header .container nav ul li a {
  color: hsl(0deg, 0%, 35%);
  font-size: 0.75rem;
}
header .container nav ul li:hover::after {
  width: 100%;
}

main .section {
  display: flex;
  justify-content: center;
}
main .section .container {
  flex-direction: column;
  gap: 2.5rem;
}
main .section .container .section-picture {
  width: 96%;
  max-width: 640px;
  aspect-ratio: 11/8;
  background-size: contain;
}
main .section .container #illustration1 {
  background-image: url(../img/illustration-1.svg);
}
main .section .container #illustration2 {
  background-image: url(../img/illustration-2.svg);
}
main .section .description-wrapper p {
  margin-bottom: 2rem;
}
main .section .description-wrapper form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin: 0 0.5rem;
}
main .section a {
  color: hsl(170deg, 45%, 43%);
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding-bottom: 0.3rem;
  border-bottom: 1px solid hsl(170deg, 45%, 43%);
  width: fit-content;
  font-size: 0.8rem;
  margin: 2.5rem auto;
}
main .section a:hover {
  opacity: 0.7;
}
main .section:nth-child(1) {
  padding: 5rem 0 9rem;
  background-image: url(../img/bg-curve-mobile.svg);
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: contain;
}
main .section:nth-child(2) {
  padding: 2rem 0 5.5rem;
  background-color: hsl(240deg, 75%, 98%);
}
main .section:nth-child(2) .section-picture {
  margin-bottom: 3rem;
}
main .section:nth-child(2) p {
  text-align: left;
  margin-bottom: 1rem;
  font-size: 0.87rem;
  color: rgba(7, 4, 57, 0.8);
  line-height: 1.3rem;
}
main .section .testimonial-card {
  width: 87.5%;
  max-width: 360px;
  margin: 0 auto;
  background-color: hsl(0deg, 0%, 99%);
  border-radius: 0.5rem;
  box-shadow: 0 0 10px 0 hsla(0deg, 0%, 0%, 0.1);
  padding: 1.3rem 1.5rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
main .section .testimonial-card img {
  margin-bottom: 0.5rem;
}
main .section .testimonial-card p {
  font-size: 0.6rem;
  line-height: 1.1rem;
  font-weight: 700;
  color: rgba(7, 4, 57, 0.7);
}
main .section .testimonial-card .profile-container {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
main .section .testimonial-card .profile-container img {
  border-radius: 50%;
  width: 1.5rem;
  aspect-ratio: 1/1;
  margin: 0;
}
main .section .testimonial-card .profile-container .details-wrapper .t-name, main .section .testimonial-card .profile-container .details-wrapper .t-role {
  margin: 0;
  line-height: 0.8rem;
}
main .section .testimonial-card .profile-container .details-wrapper .t-name {
  color: hsl(243deg, 87%, 12%);
}
main .section .testimonial-card .profile-container .details-wrapper .t-role {
  font-size: 0.5rem;
}
main .section:nth-child(3) {
  background-color: hsl(238deg, 22%, 44%);
  color: hsl(0deg, 0%, 100%);
  padding: 3rem 0 4rem;
}
main .section:nth-child(3) .container {
  width: 80%;
  gap: 1.5rem;
}
main .section:nth-child(3) .container h2 {
  font-size: 1.1rem;
}
main .section:nth-child(3) .container p {
  opacity: 0.8;
  margin: 0;
}
main .section:nth-child(3) .container form {
  display: flex;
  flex-direction: column;
  width: 80%;
  gap: 0.5rem;
}
main .section:nth-child(3) .container form input[type=email] {
  padding-left: 0.5rem;
  font-size: 0.6rem;
}
main .section:nth-child(3) .container form input[type=submit] {
  font-size: 0.7rem;
}

footer {
  background-color: hsl(243deg, 87%, 12%);
}
footer .container {
  flex-direction: column;
  align-items: flex-start;
  margin: 0 auto;
  padding: 4.5rem 5% 3rem;
  text-align: left;
  color: hsl(0deg, 0%, 100%);
}
footer .container .logo {
  margin-bottom: 2rem;
}
footer .container .links-container {
  width: 100%;
}
footer .container .links-container .contacts {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  margin-bottom: 3.5rem;
}
footer .container .links-container .contacts li {
  display: flex;
  align-items: center;
  gap: 1.2rem;
}
footer .container .links-container .internal-links {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  margin-bottom: 2.5rem;
}
footer .container .links-container .internal-links a {
  display: block;
  margin-bottom: 1rem;
  color: hsl(0deg, 0%, 100%);
  transition: color 0.2s ease-in-out;
}
footer .container .links-container .internal-links a:hover {
  color: #7a9cfa;
}
footer .container .links-container .social-links {
  display: flex;
  gap: 1rem;
  justify-content: center;
}
footer .container .links-container .social-links i {
  border: 1px solid hsl(0deg, 0%, 100%);
  border-radius: 50%;
  width: 1.8rem;
  aspect-ratio: 1/1;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: color 0.2s ease-in-out, border-color 0.2s ease-in-out;
}
footer .container .links-container .social-links i:hover {
  color: #7a9cfa;
  border-color: #7a9cfa;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 1280px;
  }
  /* TEXT */
  h1 {
    font-size: 2.4rem;
    margin-bottom: 1rem;
    line-height: 3.8rem;
  }
  h2 {
    font-size: 2.3rem;
    margin-bottom: 2.5rem;
  }
  p {
    font-size: 1.15rem;
  }
  /* INPUTS */
  input[type=email] {
    width: 100%;
    max-width: 320px;
  }
  input[type=submit] {
    width: 100%;
    max-width: 200px;
  }
  /* HEADER */
  header .container {
    padding: 4rem 0 0;
  }
  header .container img {
    max-width: 160px;
  }
  header .container nav ul {
    display: flex;
    gap: 3rem;
  }
  header .container nav ul li a {
    font-size: 1em;
  }
  /* MAIN */
  main .section .container {
    flex-direction: row-reverse;
    text-align: left;
  }
  main .section .description-wrapper {
    max-width: 600px;
  }
  main .section .description-wrapper p {
    margin-bottom: 1.5rem;
  }
  main .section .description-wrapper form {
    flex-direction: row;
    margin: 0;
  }
  main .section:nth-child(1) {
    padding: 4.5rem 0 9rem;
    background-image: url(../img/bg-curve-desktop.svg);
  }
  main .section:nth-child(2) {
    padding-bottom: 6.3rem;
  }
  main .section:nth-child(2) .description-wrapper p {
    font-size: 0.9rem;
    max-width: 500px;
    margin-bottom: 1.2rem;
  }
  main .section a {
    margin: 1.5rem 0 2.5rem;
    font-size: 0.9rem;
  }
  main .section .testimonial-card {
    margin: 0;
    padding: 1.8em 2rem;
  }
  main .section .testimonial-card p {
    line-height: 1.4rem;
  }
  main .section .testimonial-card .profile-container img {
    width: 2rem;
  }
  main .section:nth-child(3) {
    padding: 6.5rem 0 5em;
  }
  main .section:nth-child(3) .container {
    flex-direction: row;
    width: calc(100% - 50px);
  }
  main .section:nth-child(3) .container .description-wrapper {
    width: 60%;
  }
  main .section:nth-child(3) .container .description-wrapper h2 {
    font-size: 2rem;
    margin-bottom: 1.5rem;
  }
  main .section:nth-child(3) .container .description-wrapper p {
    max-width: 600px;
  }
  main .section:nth-child(3) .container form {
    width: 40%;
    gap: 1rem;
  }
  main .section:nth-child(3) .container form input {
    padding: 1rem 0;
  }
  main .section:nth-child(3) .container form input[type=email] {
    padding-left: 1.5em;
    font-size: 0.8rem;
    max-width: 480px;
  }
  main .section:nth-child(3) .container form input[type=submit] {
    font-size: 0.9em;
  }
  /* FOOTER */
  footer .container {
    padding: 4.5rem 0 3rem;
  }
  footer .container .links-container {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
  footer .container .links-container > * {
    flex: 1;
  }
  footer .container .links-container .internal-links {
    flex-direction: row;
    justify-content: space-around;
  }
}