h1, h2, nav {
    font-family: 'Raleway', sans-serif;
    font-weight: 700;
}

h1 {
    font-size: 1.5rem;
    margin-bottom: 1.75rem;
    line-height: 2.2rem;
}

h2 {
    font-size: 1rem;
    margin-bottom: 1.5rem;
}

ul {
    list-style-type: none;
}

a {
    text-decoration: none;
}

p {
    font-size: .95rem;
}