* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

:root {
    font-family: 'Open Sans', sans-serif;
    font-weight: 400;
    color: $c_txt_dark;
}

body {
    background-color: $c_bg_body;
    text-align: center;
}

.container {
    width: calc(100% - 50px);
    display: flex;
    justify-content: space-between;
    align-items: center;
}